import React, { Component } from 'react';
import './App.css';
import * as THREE from 'three';
import * as OBJLoader from 'three-obj-loader';
import * as myshader from './myshader';
//import * as OrbitControls from 'three-orbit-controls';
OBJLoader(THREE);
//OrbitControls(THREE);

const OrbitControls = require('three-orbit-controls')(THREE);

class App extends Component {
   componentDidMount() {
    var scene = new THREE.Scene();
    scene.background = new THREE.Color( 0x000000 );
    const width =  window.innerWidth;
    const height = window.innerHeight;
    var camera = window.c  = new THREE.PerspectiveCamera( 75, window.innerWidth / window.innerHeight, 0.1, 100000 );
    var renderer = new THREE.WebGLRenderer();
    renderer.setSize( window.innerWidth, window.innerHeight );
    this.appRef.appendChild( renderer.domElement );
    this.THREE = THREE;
    const loader = new this.THREE.OBJLoader();
    var controls = new OrbitControls( camera );
  //  var geometry = new THREE.BoxGeometry( 1, 1, 1 );
    //var material = 
    var uniforms = {
      time: { value: 1.0 }
    };
    var material = window.location.search.includes('a') ? new THREE.ShaderMaterial( {
      uniforms: uniforms,
      vertexShader: myshader.default.vertex,
      fragmentShader: myshader.default.fragment
    } ) : new THREE.MeshLambertMaterial( { color: 0xffffff } );
//    var cube = new THREE.Mesh( geometry, material );
  // /  scene.add( cube );
    camera.position.z = 5;
    camera.position.x = 6;
    camera.position.y = 6;
    var light = new THREE.AmbientLight( 0x505050 ); // soft white light
    scene.add( light );
    var sphere = new THREE.SphereBufferGeometry( 0.5, 16, 8 );  
    [
      [0,1,0,0x80ff80, .1],
      [-6,0,-5, 0xff8080,1],
      [6,0,5, 0x0080ff,1],
      [0,-6,0,0x80ff80,.5],
  ].forEach(pos => {
      
      var light3 = new THREE.PointLight( pos[3], pos[4], 30 );
      
          //light3.add( new THREE.Mesh( sphere, new THREE.MeshBasicMaterial( { color: 0x80ff80 } ) ) );
          scene.add( light3 );
          light3.position.x = pos[0];
      light3.position.y = pos[1];
      light3.position.z = pos[2];
    });
    

    loader.load(
      'a.obj',
      function ( object ) {
        console.log('olad ok!');
        object.traverse( function ( child ) {
          if ( child instanceof THREE.Mesh ) { 
              child.material = material;
          }
      } );
        scene.add( object );
        // fitCameraToObject(camera, object)        
},
      function ( xhr ) {
        console.log( ( xhr.loaded / xhr.total * 100 ) + '% loaded' );
      },
      function ( error ) {
        console.log( 'An error happened' );
      }
    );
    const startTime = Date.now();
    function animate(timestamp) {
      requestAnimationFrame( animate );
      renderer.render( scene, camera );
      controls.update();
      var elapsedMilliseconds = Date.now() - startTime;
		  var elapsedSeconds = elapsedMilliseconds / 1000.;
		  uniforms.time.value = 2. * elapsedSeconds;
    }
    animate(0);
  }
  
  render() {
    return (
      <div ref={r=>this.appRef = r}>
      </div>
    );
  }
}

export default App;

 function fitCameraToObject( camera, object, offset, controls ) {

  offset = offset || 1.25;

  const boundingBox = new THREE.Box3();

  // get bounding box of object - this will be used to setup controls and camera
  boundingBox.setFromObject( object );

  const center = boundingBox.getCenter();

  const size = boundingBox.getSize();

  // get the max side of the bounding box (fits to width OR height as needed )
  const maxDim = Math.max( size.x, size.y, size.z );
  const fov = camera.fov * ( Math.PI / 180 );
  let cameraZ = Math.abs( maxDim / 4 * Math.tan( fov * 2 ) );

  cameraZ *= offset; // zoom out a little so that objects don't fill the screen

  camera.position.z = cameraZ;

  const minZ = boundingBox.min.z;
  const cameraToFarEdge = ( minZ < 0 ) ? -minZ + cameraZ : cameraZ - minZ;

  camera.far = cameraToFarEdge * 3;
  camera.updateProjectionMatrix();

  if ( controls ) {

    // set camera to rotate around center of loaded object
    controls.target = center;

    // prevent camera from zooming out far enough to create far plane cutoff
    controls.maxDistance = cameraToFarEdge * 2;

    controls.saveState();

  } else {

      camera.lookAt( center )

 }
}


